import { Component, OnInit } from '@angular/core';
import {NzMessageService, UploadFile} from 'ng-zorro-antd';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
declare var require;
var E = require('wangeditor');
@Component({
  selector: 'app-add-consulting',
  templateUrl: './add-consulting.component.html',
  styleUrls: ['./add-consulting.component.scss']
})
export class AddConsultingComponent implements OnInit {
  validateForm: FormGroup;
  patt = /\w{1,100}/;
  edit:any;
  loading = false;
  avatarUrl: string;
  life = false;
  imgUrl;
  imgUpLoad = '';
  constructor(private url :UrlServeService,private http: ResetHttpService,private fb: FormBuilder,private utilServe:UtilServeService,private msg:NzMessageService) {
    this.validateForm = this.fb.group({
      consultingList   : [ '', [ Validators.required ] ],
      consultingTitle: [ '', [ Validators.required ] ]
    });
    this.imgUrl = this.url.commentBaseUrl+this.url.imgUpLoad;
  }

  ngOnInit() {
    // 创建编辑器
    this.edit = new E('#edit')
    this.edit.create();
  }
//提交
  submitForm = ($event, value) => {
    console.log(value);
    var that = this;
    //获取html
    var content = this.edit.txt.html();
    console.log(content);
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(content === '<p><br></p>'){
      console.log(12);
      this.life = true;
      return;
    }else{
      this.life = false;
    }
    // if(!this.imgUpLoad) {
    //   this.msg.error('请上传图片！');
    //   return;
    // }
    if(!this.validateForm.valid) return;
    this.http.get(this.url.addNews,{
      type:this.validateForm.controls['consultingList'].value,
      title:this.validateForm.controls['consultingTitle'].value,
      image:this.imgUpLoad,
      content:content
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('资讯添加成功！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
      this.msg.error(err);
    })
  };
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //验证用户名
  checkName = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  }
  //上传
  beforeUpload = (file: File) => {
    const isJPG = file.type === 'image/jpeg';
    const isPng = file.type === 'image/png';
    if(!(isPng || isJPG)){
      this.msg.error('只允许上传JPG 或者 PNG格式的图片');
    }
    const isLt2M = file.size / 1024 / 1024 < 1;
    if (!isLt2M) {
      this.msg.error('大小不能超过2MB!');
    }
    return (isJPG || isPng) && isLt2M;
  }

  private getBase64(img: File, callback: (img: {}) => void): void {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  handleChange(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.imgUpLoad = info.file.response.response.filePath;
      }
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (img: string) => {
        this.loading = false;
        this.avatarUrl = img;
      });
    }
  }


}
