import { Component, OnInit } from '@angular/core';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {Router} from '@angular/router';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';

@Component({
  selector: 'app-consulting-manger',
  templateUrl: './consulting-manger.component.html',
  styleUrls: ['./consulting-manger.component.scss']
})
export class ConsultingMangerComponent implements OnInit {
  dataSet = [];
  constructor(private util:UtilServeService,private http: ResetHttpService,private router:Router,private modalService:NzModalService,private url:UrlServeService,private msg:NzMessageService) {

  }

  ngOnInit() {
    this.getData();
  }

  //获取数据
  getData(pnum = '',rnum = ''){
    this.http.get(this.url.newsList,{
      page:pnum,
      rows:rnum
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      this.msg.error(err);
    })
  }
  //跳转
  goPager(option){
    this.util.goPage(option);
  }
  //删除
  dele(data){
    var confirmModal = this.modalService.confirm({
      nzTitle: '删除资讯',
      nzContent: '删除操作将同时删除前端用户的资讯内容，确认删除吗？',
      nzOnCancel:()=>{confirmModal.destroy()},
      nzOnOk: () => new Promise((resolve, reject) => {
        this.http.get(this.url.deleteNews,{
          id:data.id
        },data=>{
          console.log(data);
          if(data.code == '001000'){
            this.msg.success('删除成功！');
            this.getData();
          }else{
            this.msg.error(data.message);
          }
        },err=>{
          this.msg.error(err);
        })
        confirmModal.destroy()
      }).catch(() => console.log('Oops errors!'))
    });
  }
}
