import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {EditConsultingComponent} from './edit-consulting/edit-consulting.component';
import {ConsultingMangerComponent} from './consulting-manger/consulting-manger.component';
import {AddConsultingComponent} from './add-consulting/add-consulting.component';

const routes: Routes = [
  {path:'',component:ConsultingMangerComponent,data:{breadcrumb: '资讯管理'}},
  {path:'add-consulting',component:AddConsultingComponent,data:{breadcrumb: '添加资讯'}},
  {path:'edit-consulting',component:EditConsultingComponent,data:{breadcrumb: '编辑资讯'}}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ConsultingRoutingModule { }
